import React from 'react';
import './MainMenu.css'
import MenuIcon from '../../assets/images/menuicon.svg';

const MainMenu = ({saved, gameState})=> {
 return (<div className='mainmenu-root'>
            <div className="Logo">
                <p>--- THE BATTLE OF ---</p>
                <h1>POLYTOPIA</h1>
            </div>
            <div>
                <h4 onClick={()=>{gameState("GAME")}}>NEW GAME</h4>
                {saved ? <h4 onClick={()=>{gameState("RESUME")}}>RESUME GAME</h4>: null}
                <h4 onClick={()=>{gameState("MULTIPLAYER")}}>MULTIPLAYER</h4>
            </div>
            <div className="mainmenu-buttons">
                <div>
                    <div onClick={()=>{gameState("SETTINGS")}}>
                        <img src={MenuIcon} alt="settings"/>
                    </div>
                    <p>Settings</p>
                </div>
                <div>
                    <div onClick={()=>{gameState("HIGHSCORES")}}>
                        <img src="" alt="hightScore"/>
                    </div>
                    <p>High Score</p>
                </div>
                <div>
                    <div onClick={()=>{gameState("THRONEROOM")}}>
                        <img src="" alt="ThroneRoom"/>
                    </div>
                    <p>Throne Room</p>
                </div>
                <div>
                    <div onClick={()=>{gameState("ABOUT")}}>
                        <img src="" alt="about"/>
                    </div>
                    <p>About</p>
                </div>
            </div>
        </div>);
}
export default MainMenu;