import React from 'react';
import TestCube from '../../assets/images/cube.svg';
import './BlockUI.css';


/**
 * Creates the Ui when a block is slected in the World
 * @param {string} name the name of the block that was click 
 * @param {object} blockData the THREEJS Mesh userData object
 * @param {function} update redux function to remove this element from the Screen
 * @param {function} isHover function to determine when the cuser is on a ui element 
 */
const BlockUI = ({name, blockData,update, isHover}) => {
  const displayName = `${name}${blockData.attached.length > 0 ? ", " + blockData.attached.map(item=>item.type.toLowerCase().padStart(5," ,")) : "" }`;

  return (<div className="blockUI-root" onMouseEnter={()=>{isHover(true)}} onMouseLeave={()=>{isHover(false)}} onTouchEnd={()=>{console.log("TouchEnd")}} onTouchStart={()=>{console.log("TouchStart")}}>
  <ul className="blockUI-data">
    <li className="block-UI">
      <img src={TestCube} alt="ImageIcon"/>
    </li>
    <li className="block-UI">
      <p>{displayName}</p>
      <p></p>
    </li>
    <li><button onClick={()=>{update(false);isHover(false);}}>^</button></li>
  </ul>
  <div className="blockUI-build">
    {blockData.attached.length > 0 ? blockData.attached.map(block => {
      return (<div className="block-buildRoot" key={`${name}_${block.type}`} onClick={()=>{block.build(block.position, block.type); blockData.attched = blockData.attached.splice(0,1); /* <== This will not work later !!! */}}>
                <div>
                  <p><span>✯</span>{block.cost}</p>
                  <img src={TestCube} alt=""/>
                </div>
                <p>{block.display}</p>
              </div>);
    }) : null 
    }
  </div>
</div>);
}

export default BlockUI;
