import FieldMtl from '../assets/models/field/field.mtl';

import FruitOneObj from '../assets/models/fruit/fruit_one.obj';
import FruitOneMtl from '../assets/models/fruit/fruit_one.mtl';

import OceanObj from '../assets/models/ocean/ocean.obj';
import OceanNoneObj from '../assets/models/ocean/ocean_none.obj';
import OceanOneObj from '../assets/models/ocean/ocean_one.obj';
import OceanTwoObj from '../assets/models/ocean/ocean_two.obj';
import OceanTwoBObj from '../assets/models/ocean/ocean_two_b.obj';
import OceanThreeObj from '../assets/models/ocean/ocean_three.obj';
import OceanMtl from '../assets/models/ocean/ocean.mtl';
import OceanNoneMtl from '../assets/models/ocean/ocean_none.mtl';
import OceanOneMtl from '../assets/models/ocean/ocean_one.mtl';
import OceanTwoMtl from '../assets/models/ocean/ocean_two.mtl';
import OceanTwoBMtl from '../assets/models/ocean/ocean_two_b.mtl';
import OceanThreeMtl from '../assets/models/ocean/ocean_three.mtl';

import WaterObj from '../assets/models/water/water.obj';
import WaterNoneObj from '../assets/models/water/water_none.obj';
import WaterOneObj from '../assets/models/water/water_one.obj';
import WaterTwoObj from '../assets/models/water/water_two.obj';
import WaterTwoBObj from '../assets/models/water/water_two_b.obj';
import WaterThreeObj from '../assets/models/water/water_three.obj';
import WaterMtl from '../assets/models/water/water.mtl';
import WaterNoneMtl from '../assets/models/water/water_none.mtl';
import WaterOneMtl from '../assets/models/water/water_one.mtl';
import WaterTwoMtl from '../assets/models/water/water_two.mtl';
import WaterTwoBMtl from '../assets/models/water/water_two_b.mtl';
import WaterThreeMtl from '../assets/models/water/water_three.mtl';

import CloudObj from '../assets/models/other/cloud.obj';
import CloudMtl from '../assets/models/other/cloud.mtl';

import SlectorObj from '../assets/models/other/slector.obj';
import SlectorNoneMtl from '../assets/models/other/slector.mtl';
import slectorFactionMtl from '../assets/models/other/slectorFaction.mtl';

import TreeOneObj from '../assets/models/trees/tree_one.obj';
import TreeTwoObj from '../assets/models/trees/tree_two.obj';
import TreeOneMtl from '../assets/models/trees/tree_one.mtl';
import TreeTwoMtl from '../assets/models/trees/tree_two.mtl';

import backgroundImage from '../assets/images/background_game.jpg';


export const updateGameState = (currentPlayer, players) => dispatch =>{
  dispatch({type: constants.GAME_STATE, payload: { currentPlayer, players}})
}
export const changeOnHover = state => dispatch =>{
  dispatch({type: constants.HOVER_OVER_UI, payload: state});
}
export const slectedChanged = state => dispatch =>{
  dispatch({type: constants.HAS_SLECTED_BLOCK, payload: state});
}
export const changeGameState = (state) => dispatch =>{
  switch (state) {
    case "GAME":
        dispatch({type: constants.GAME_STATE_GAME});
      break;
    case "MAINMENU":
        dispatch({type: constants.GAME_STATE_MAINMENU});
      break;
    case "LOADER":
        dispatch({type: constants.GAME_STATE_LOADER});
      break;
    case "ABOUT":
        dispatch({type: constants.GAME_STATE_ABOUT});
      break;
    default:
        dispatch({type: constants.GAME_STATE_LOADER});
      break;
  }
}
export const worldLoading = (state,error) => dispatch =>{
  switch (state) {
    case true:
      dispatch({type: constants.WORLD_LOADING_PENDING});
      break;
    case false: 
      dispatch({type: constants.WORLD_LOADING_FINISH});
      break;
    default:
        dispatch({type: constants.WORLD_LOADING_ERROR, payload: error});
      break;
  }
}
export const loadingGameContent = () => dispatch =>{
    let models = {},config = {}
    const manager = new LoadingManager();
    manager.onStart =  () =>{ dispatch({type: constants.GAME_LOADING_PENDING}); };
    manager.onLoad =  ( ) => {  dispatch({type: constants.GAME_LOADING_FINISH, payload: { models, config }    });};
    manager.onError = err => { dispatch({type: constants.GAME_LOADING_ERROR, payload: err});};
    manager.onProgress =  ( url, itemsLoaded, itemsTotal ) => { dispatch({type: constants.GAME_LOADING_PROGRESS, payload: {item: itemsLoaded, of:itemsTotal}}) };
    
    const configLoader = new FileLoader(manager).setPath('./config/').setResponseType('json');
    const mtlLoader = new MTLLoader(manager).setTexturePath('./textures/');
    const textureLoader = new TextureLoader(manager);
    
    configLoader.load('config.json', json=>{
         config = json;
    });


    textureLoader.load(backgroundImage,(texture)=>{
      models.textures = {
        background: texture
      }
    });

      mtlLoader.load(FieldMtl,materials=>{
        materials.preload();
        const objLoader = new OBJLoader(manager).setMaterials(materials);
        objLoader.load(FieldObj,object=>{
         models.field = {
           geometry: object.children[0].geometry,
           material: object.children[0].material
         }
        });
      });

      mtlLoader.load(CloudMtl,materials=>{
        materials.preload();
        const objLoader = new OBJLoader(manager).setMaterials(materials)
        objLoader.load(CloudObj,object=>{
         models.cloud = {
           geometry: object.children[0].geometry,
           material: object.children[0].material
         }
        });
      });

      mtlLoader.load(WallMtl,materials=>{
        materials.preload();
        const objLoader = new OBJLoader(manager).setMaterials(materials)
        objLoader.load(WallModel,object=>{
         models.wall = {
           geometry: [object.children[0].geometry],
           material: [object.children[0].material]
         }
        });
      });

      mtlLoader.load(TreeOneMtl,materials=>{
        materials.preload();
        const objLoader = new OBJLoader(manager).setMaterials(materials)
        objLoader.load(TreeOneObj,object=>{
         models.tree = {
           geometry: [object.children[0].geometry],
           material: [object.children[0].material]
         }
         mtlLoader.load(TreeTwoMtl,materials=>{
          materials.preload();
          const objLoader = new OBJLoader(manager).setMaterials(materials)
          objLoader.load(TreeTwoObj,object=>{
           models.tree.geometry.push(object.children[0].geometry);
           models.tree.material.push(object.children[0].material);
          });
        });
        });
      });

      mtlLoader.load(WaterMtl,materials=>{
        materials.preload();
        const objLoader = new OBJLoader(manager).setMaterials(materials)
        objLoader.load(WaterObj,object=>{
         models.water = {
           geometry: [object.children[0].geometry],
           material: [object.children[0].material]
         }
         mtlLoader.load(WaterThreeMtl,materials=>{
          materials.preload();
          const objLoader = new OBJLoader(manager).setMaterials(materials)
          objLoader.load(WaterThreeObj,object=>{
           models.water.geometry.push(object.children[0].geometry);
           models.water.material.push(object.children[0].material);
          });
        });
         mtlLoader.load(WaterTwoMtl,materials=>{
          materials.preload();
          const objLoader = new OBJLoader(manager).setMaterials(materials)
          objLoader.load(WaterTwoObj,object=>{
           models.water.geometry.push(object.children[0].geometry);
           models.water.material.push(object.children[0].material);
          });
        });
        mtlLoader.load(WaterOneMtl,materials=>{
          materials.preload();
          const objLoader = new OBJLoader(manager).setMaterials(materials)
          objLoader.load(WaterOneObj,object=>{
           models.water.geometry.push(object.children[0].geometry);
           models.water.material.push(object.children[0].material);
          });
        });
        mtlLoader.load(WaterNoneMtl,materials=>{
          materials.preload();
          const objLoader = new OBJLoader(manager).setMaterials(materials)
          objLoader.load(WaterNoneObj,object=>{
           models.water.geometry.push(object.children[0].geometry);
           models.water.material.push(object.children[0].material);
          });
        });

        mtlLoader.load(WaterTwoBMtl,materials=>{
          materials.preload();
          const objLoader = new OBJLoader(manager).setMaterials(materials)
          objLoader.load(WaterTwoBObj,object=>{
           models.water.geometry.push(object.children[0].geometry);
           models.water.material.push(object.children[0].material);
          });
        });

        });
      });




      mtlLoader.load(SlectorNoneMtl,materials=>{
        materials.preload();
        const objLoader = new OBJLoader(manager).setMaterials(materials)
        objLoader.load(SlectorObj,object=>{
         models.slector = {
           geometry: object.children[0].geometry,
           material: [object.children[0].material]
         }
         mtlLoader.load(slectorFactionMtl,materials=>{
          materials.preload();
          models.slector.material.push(materials.materials.Slector)
        });
        });
      });

      mtlLoader.load(OceanMtl,materials=>{
        materials.preload();
        const objLoader = new OBJLoader(manager).setMaterials(materials)
        objLoader.load(OceanObj,object=>{
         models.ocean = {
           geometry: [object.children[0].geometry],
           material: [object.children[0].material]
         }
         mtlLoader.load(OceanThreeMtl,materials=>{
          materials.preload();
          const objLoader = new OBJLoader(manager).setMaterials(materials)
          objLoader.load(OceanThreeObj,object=>{
            models.ocean.geometry.push(object.children[0].geometry);
            models.ocean.material.push(object.children[0].material);
          });
        });
         mtlLoader.load(OceanTwoMtl,materials=>{
          materials.preload();
          const objLoader = new OBJLoader(manager).setMaterials(materials)
          objLoader.load(OceanTwoObj,object=>{
           models.ocean.geometry.push(object.children[0].geometry);
           models.ocean.material.push(object.children[0].material);
          });
        });
        mtlLoader.load(OceanOneMtl,materials=>{
          materials.preload();
          const objLoader = new OBJLoader(manager).setMaterials(materials)
          objLoader.load(OceanOneObj,object=>{
           models.ocean.geometry.push(object.children[0].geometry);
           models.ocean.material.push(object.children[0].material);
          });
        });
        mtlLoader.load(OceanNoneMtl,materials=>{
          materials.preload();
          const objLoader = new OBJLoader(manager).setMaterials(materials)
          objLoader.load(OceanNoneObj,object=>{
           models.ocean.geometry.push(object.children[0].geometry);
           models.ocean.material.push(object.children[0].material);
          });
        });
        mtlLoader.load(OceanTwoBMtl,materials=>{
          materials.preload();
          const objLoader = new OBJLoader(manager).setMaterials(materials)
          objLoader.load(OceanTwoBObj,object=>{
           models.ocean.geometry.push(object.children[0].geometry);
           models.ocean.material.push(object.children[0].material);
          });
        });

        });
      });

      mtlLoader.load(FruitOneMtl,materials=>{
        materials.preload();
        const objLoader = new OBJLoader(manager).setMaterials(materials)
        objLoader.load(FruitOneObj,object=>{
         models.fruit = {
           geometry: [object.children[0].geometry],
           material: [object.children[0].material]
         }
        });
      });
    
      
      
}


