import { Random } from 'random-js';
import SimplexNoise from 'simplex-noise';
import CREATE from '../Create/createBlockMesh';
/**
 * Credit for noise map generation
 *   https://www.redblobgames.com/maps/terrain-from-noise/
 *
 *  Class for generating a world
 *
 *
 *
 */
export default class WorldGeneration_V2 {
    constructor(config) {
        this.config = config;
        this.models = new CREATE(config.models);
        this.genNoise = new SimplexNoise();
        this.worldCount = -1;
        /**
         * @param {number} height the x axis of the world
         * @param {number} width the y axis of the world
         * @protected
         * @memberof WorldGeneration_V2
         */
        this.createWorldSeed = (height, width) => {
            let value = [];
            for (let y = 0; y < height; y++) {
                value[y] = [];
                for (let x = 0; x < width; x++) {
                    let nx = x / width - 0.5, ny = y / height - 0.5;
                    value[y][x] = this.noise(nx, ny);
                }
            }
            return value;
        };
        /**
         * Creates a world and then adds it to the THREEJS scene Object
         *
         * @memberof WorldGeneration_V2
         */
        this.genWorld = () => {
            this.config.state[0](true);
            const { Slector} = this.models;
            const { worldSize, factions } = this.config;
            this.worldSeedArray = [];
            this.worldArray = [];
            this.coordZ = 0;
            this.coordX = 0;
            this.row = 0;
            this.colum = 0;
            let seed = this.createWorldSeed(worldSize, worldSize);
            seed.forEach(array => {
                array.forEach(int => {
                    this.worldSeedArray.push(this.intToBlock(int));
                });
            });
            
            this.blockView = [];
            let currentRow = worldSize;
            let lastRow = 0;
            let slicedWorld = [];
            for(let i = 0; i <= (worldSize-1); i++){
                slicedWorld.push(this.worldSeedArray.slice(lastRow,currentRow));
                lastRow = currentRow;
                currentRow += worldSize;
            }
           
           slicedWorld.forEach((item, index)=>{
                item.forEach((seconditem, i)=>{
                    switch (seconditem) {
                        case "WATER":
                                let countWater = 0;
                                slicedWorld[index][i-1] === "WATER" ? countWater++ : countWater+=0; 
                                slicedWorld[index][i+1] === "WATER" ? countWater++ : countWater+=0; 
                                slicedWorld[index+1] === undefined ? countWater+=0 : slicedWorld[index+1][i] === "WATER" ? countWater++ : countWater+=0;
                                slicedWorld[index-1] === undefined ? countWater+=0 : slicedWorld[index-1][i] === "WATER" ? countWater++ : countWater+=0;
                                if(countWater === 2){
                                    slicedWorld[index][i+1] === "WATER" && slicedWorld[index][i-1] === "WATER" ? countWater = 5 : countWater+=0; 
                                }
                                this.blockView.push(countWater);
                            break;
                        case "OCEAN":
                                let countOcean = 0;
                                slicedWorld[index][i-1] === "OCEAN" ? countOcean++ : countOcean+=0; 
                                slicedWorld[index][i+1] === "OCEAN" ? countOcean++ : countOcean+=0; 
                                slicedWorld[index+1] === undefined ? countOcean+=0 : slicedWorld[index+1][i] === "OCEAN" ? countOcean++ : countOcean+=0;
                                slicedWorld[index-1] === undefined ? countOcean+=0 : slicedWorld[index-1][i] === "OCEAN" ? countOcean++ : countOcean+=0;
                                if(countOcean === 2){
                                    slicedWorld[index][i+1] === "OCEAN" && slicedWorld[index][i-1] === "OCEAN" ? countOcean = 5 : countOcean+=0; 
                                }
                                this.blockView.push(countOcean);
                            break;
                        default:
                            this.blockView.push(0);
                            break;
                    }
                })
            });

            this.worldSeedArray.forEach(type => {
                this.addToWorld(type, this.models);
                this.row++;
                this.coordZ += 0.5;
                if (this.row > (worldSize - 1)) {
                    this.colum++;
                    if (this.colum < worldSize) {
                        this.row = 0;
                        this.coordZ = 0;
                        this.coordX += 0.5;
                    }
                }
            });
            for (let i = 0; i < config.config.settings.world.max_fruit; i++) { this.addFruit(this.models); }
            for (let i = 0; i < config.config.settings.world.max_trees; i++) { this.addTrees(this.models); } 
            // add Slector Boxs
            for (let i = 0; i < factions.length; i++) { this.config.scene.add(Slector(factions[i], -(i+1))); }

            this.addToScene(this.worldArray);
            this.config.state[0](false);
        };
        /**
         * Add the THREEJS objects to the @param worldArray to be add to the THREEJS Scene
         * @param {string} type the type to push to be pushed to the worldArray
         * @protected
         * @memberof WorldGeneration_V2
         */
        this.addToWorld = (type, { Field, Ocean, Water }) => {
            switch (type) {
                case "OCEAN":    
                    this.worldCount++;
                    this.worldArray.push(Ocean({ x: this.coordX, y: 0, z: this.coordZ }, null, this.blockView[this.worldCount], 0));
                    return;
                case "FIELD":
                    this.worldCount++;
                    this.worldArray.push(Field({ x: this.coordX, y: 0, z: this.coordZ }, null));
                    return;
                case "WATER":
                    this.worldCount++;
                    this.worldArray.push(Water({ x: this.coordX, y: 0, z: this.coordZ }, null, this.blockView[this.worldCount], 0));
                    return;
                default:
                    return;
            }
        };
        /**
         * adds objects from the @param worldArray to THREEJS Scene
         * @param {object[]} array the array to add the world to the scene
         * @protected
         * @memberof WorldGeneration_V2
         */
        this.addToScene = (array) => {
            array.forEach(item => {
                this.config.scene.add(item);
            });
        };
        /**
         * turns the noise array into a string array detrming where blocks go in the world
         * @param {number} int number to be made into a string
         * @protected
         * @memberof WorldGeneration_V2
         */
        this.intToBlock = (int) => {
            if (int <= 0.25) {
                return "OCEAN";
            }
            else if (int < 0.45 && int > 0.25) {
                return "WATER";
            }
            else {
                return "FIELD";
            }
        };
        /**
         * Add the World object of Fruit to the given world
         * @param {object} model
         * @protected
         * @memberof WorldGeneration_V2
         */
        //
        //
        this.addFruit = ({ Fruit }) => {
          const random = new Random();
           let block = random.pick(this.worldArray);
           let data = block.userData;

           if(data.type === "Field"){
                if(data.attached.length > 0){
                     this.addFruit({Fruit});
                }else{
                    data.attached.push({position: {x: block.position.x, z: block.position.z  },type: "Fruit", cost: 2, display: "Harvest Fruit", variation: 0, tooltip: "",build: (pos,typeToRemove)=>{this.harvestObject(pos,typeToRemove)} });
                    this.worldArray.push(Fruit({ x: block.position.x, y: 0, z: block.position.z }, null));
                }
           }else{
            this.addFruit({Fruit});
           }
        }
    }
    /**
     * finds the object at the give coords
     * @param {number} x 
     * @param {nubmer} y
     * @memberof WorldGeneration_V2
     * @returns {THREE.Mesh} THREE.Mesh object 
     */
    findObjectAt = ({x,z}) =>{
       return this.worldArray.find(i=>{return (i.position.x === x && i.position.z === z)});
    }
    addTrees({ Tree }){
        const random = new Random();
        let block = random.pick(this.worldArray);
        let data = block.userData;
        if(data.type === "Field"){
            if(data.attached.length > 0){
                this.addTrees({ Tree });
            }else{
            data.attached.push({position: {}, type: "Tree", variation: null, tooltip:""});
            this.worldArray.push(Tree({ x: block.position.x, y: 0, z: block.position.z }, null, random.integer(0,1)));
            }
        }else{
            this.addTrees({ Tree });
        }
    }
    /**
     * @protected
     * @param {number} nx
     * @param {number} ny
     * @returns {number}
     * @memberof WorldGeneration_V2
     */
    noise(nx, ny) {
        return this.genNoise.noise2D(nx, ny) / 2 + 0.5; // Rescale from -1.0:+1.0 to 0.0:1.0
    }
    /**
     *
     *
     * @memberof WorldGeneration_V2
     */
    harvestObject = (pos,removeType) => {
             // add population to closet city and what not to the current user
             this.deleteObject({x:pos.x,z:pos.z},removeType);
    }

    buildObject = () =>{
            // add population to closet city and what not to the current user
    }
   /**
    * 
    * @param {object} {x,y} the postion of the block
    * @param {string} type type to remove
    */
    deleteObject({x,z},type){
    let scene = this.config.scene;
    
     let itemToDelete = scene.children.find(item=>{
        return item.position.x === x && item.position.z === z && item.userData.type === type;
      });
      //console.log(itemToDelete)
      itemToDelete.geometry.dispose();
      itemToDelete.material.dispose();
      scene.remove(itemToDelete);
  
    }
    /**
     *
     *
     * @returns the world array as json
     * @memberof WorldGeneration_V2
     */
    toJson(){
        return JSON.stringify({ world: this.worldArray});
    }
}
