import React, {Component} from 'react';
import {connect} from 'react-redux';
import App from '../App/App';
import {REVISION} from 'three' 
import MainMenu from '../../components/MainMenu/MainMenu';
import Loader from '../../components/Loaders/Loader';
import LoadingFiles from '../../components/Loaders/LoadingFiles';
import About from '../../components/About/About';
import ErrorBoundry from '../Errorboundery/Errorboundery'
import {loadingGameContent,changeGameState} from '../../redux/actions';
import './index.css'


const mapStateToProps = state => {return {
    error: state.gameLoading.error,
    progress: state.gameLoading.progress,
    isPending: state.gameLoading.isPending,
    isLoading: state.worldLoading.isLoading,
    location: state.gameState.location,
    config: state.gameLoading.config
  }};
  const mapDispatchToProps = dispatch =>{
    return {
       loadGameAssets: ()=>dispatch(loadingGameContent()),
       changeState: state=>dispatch(changeGameState(state))
    }
  }

class Polytopia extends Component{
    constructor(){
        super();
        this.state = {
            savedGame: false,
            savedGameObject: {}
        }
    }

    componentWillMount(){
        this.props.loadGameAssets();
        let savedGame = window.localStorage.getItem('save');
        if(savedGame){
            this.setState({savedGame: true, savedGameObject: savedGame});
        }
    }

    componentDidMount(){
    }
    componentDidUpdate(){
    }

    displayContent = (state)=>{
        switch (state) {
            case "GAME":
                return (<ErrorBoundry><App/></ErrorBoundry>);
            case "MAINMENU":
                return <div className="background"><MainMenu saved={this.state.savedGame} gameState={this.props.changeState}/><LoadingFiles item={this.props.progress.item} of={this.props.progress.of}/></div>;
            case "LOADER": 
                return <div className="background"><Loader/></div>;
            case "ABOUT":
                return <div className="background"><About build={this.props.config.build} THREE={REVISION}/></div>
            default:
                return null;
        }
    }

 render(){ return (<div>
                     {
                         this.displayContent(this.props.location)
                     }
                     { 
                         //<App/>
                     }
                    </div>);}
}
export default connect(mapStateToProps, mapDispatchToProps)(Polytopia);