import React from 'react';
import './About.css'
const About = ({THREE, build})=> {
 return (<div className="about">
            <h1>-ABOUT-</h1>
            <a href="https://www.github.com">Github</a>
            <div>
                <h3>-Thanks to-</h3>
                <p>Blender 2.8</p>
            </div>
            <div>
                <h3>-Debug info-</h3>
                <p>Build version . . .  {build}</p>
                <p>THREE Revision . . . . . . . . . . {THREE}</p>
            </div>
        </div>);
}
export default About;