import React from 'react'; 
import './GameInfo.css'
const GameInfo = ({score, stars,turn})=> {
 return (<div className="gameinfo-root">
            <div>
                <h6>Score</h6>
                <p>{score}</p>
            </div>
            <div>
                <h6>Stars (+{stars.get})</h6>
                <p><span className="star">✯</span>{stars.have}</p>
            </div>
            <div>
                <h6>Turn</h6>
                <p>{turn}</p>
            </div>
         </div>);
}
export default GameInfo;