import React from 'react';
import './GameNav.css';
import TechTree from './assets/TechButton.png';
import Checkmark from './assets/checkmark.svg';
import MenuIcon from '../../assets/images/menuicon.svg'

const GameNav = ({image,placement,state, isHover}) => {
  return (<div className="nav-root" onMouseEnter={()=>{isHover(true)}} onMouseLeave={()=>{isHover(false)}} onTouchEnd={()=>{console.log("TouchEnd")}} onTouchStart={()=>{console.log("TouchStart")}}>
           <div onClick={()=>{console.log("Open Settings")}}>
              <div className="nav-menu">
                <img src={MenuIcon} alt="Menu Icon"/>
              </div>
              <p>Menu</p>
            </div>
            <div onClick={()=>{console.log("Open Game Stats")}}>
              <div className="nav-stats">
                <p>{placement}</p>
                <img alt="Play Icon" src={`./images/${image}.png`}/>
              </div>
              <p>Game Stats</p>
            </div>
            <div onClick={()=>{console.log("Open Tech Tree")}}>
              <div className="nav-tech">
                <img src={TechTree} alt="Tech Tree"/>
              </div>
              <p>Tech Tree</p>
            </div>
           {buttonColor(state)}
          </div>);
}

function buttonColor(state){
  switch (state) {
    case "finish":
      return ( <div onClick={()=>{console.log("Next Turn")}}>
      <div className="nav-gameover">
        <img src={Checkmark} alt=""/>
      </div>
      <p>Finish Game</p>
    </div>);
    case "next-move":
    return ( <div onClick={()=>{console.log("Next Turn")}}>
    <div className="nav-nextMove">
      <img src={Checkmark} alt=""/>
    </div>
    <p>Next Turn</p>
  </div>);
    default:
        return ( <div onClick={()=>{console.log("Next Turn")}}>
      <div className="nav-next">
        <img src={Checkmark} alt=""/>
      </div>
      <p>Next Turn</p>
    </div>);
  }
}

export default GameNav;
