import React, {Component} from 'react';
import * as THREE from 'three';
import { EffectComposer } from '../../three/EffectComposer';
import { RenderPass } from '../../three/RenderPass';
import { SMAAPass } from '../../three/SMAAPass';
import { connect } from 'react-redux';
import CameraControls from 'camera-controls';
import { loadingGameContent,  worldLoading, changeOnHover, updateGameState, slectedChanged } from '../../redux/actions'
import WorldGeneration_V2 from '../../components/WorldGeneration/WorldGeneration_2_1';
import BlockUI from '../../components/BlockUI/BlockUI';
import GameNav from '../../components/GameNav/GameNav';
import GameInfo from '../../components/GameInfo/GameInfo';
import './App.css';


/**
 *  redux mapStateToProps
 * @param {state} state 
 */
const mapStateToProps = state => {return {
  error: state.gameLoading.error,
  models: state.gameLoading.models,
  config: state.gameLoading.config,
  isLoading: state.worldLoading.isLoading,
  hovering: state.isHovering.hovering,
  currentPlayer: state.gameStateManager.currentPlayer,
  playerData: state.gameStateManager.playersData,
  hasSlectedBlock: state.isHovering.hasSlected
}};

/**
 * redux mapDispatch to Props
 * @param {redux} dispatch 
 */
const mapDispatchToProps = dispatch =>{
  return {
     loadGameAssets: ()=>dispatch(loadingGameContent()),
     changeWorldState:state=>dispatch(worldLoading(state)),
     isHovering: bool=>dispatch(changeOnHover(bool)),
     updateGameState: (currentPlayer, players)=>dispatch(updateGameState(currentPlayer, players)),
     hasSlected: state=>dispatch(slectedChanged(state))
  }
}
const initalSlectedState = { attached:[]};
class App extends Component{
  constructor(){
    super();
    this.state = {
      isBlockSlected: false,
      slectedData: initalSlectedState
    }
  }
  /**
   * @memberof App
   */
  componentWillMount(){
    // ? "Avoid introducing any side effect..." This is a side-effect =>
    this.props.updateGameState(0,[
      {
       faction: "Imperius", 
       score:0 ,
       stars:{get: 0, have:0} ,
       turn:0, 
       playerName: "", 
       placement:"1st", 
       image:"ImperiusHead", 
       state:"next"
      }]); // load from menu
  }
  /**
   * @memberof App
   */
  componentDidMount(){
    // THREE Scene
   this.scene = new THREE.Scene();
   this.clock = new THREE.Clock();
   this.raycaster = new THREE.Raycaster();
   CameraControls.install( { THREE: THREE } );
   // renderer 
   this.renderer = new THREE.WebGLRenderer({antialias: true, logarithmicDepthBuffer: false});
   this.renderer.shadowMap.enabled = true;
   this.renderer.autoClear = false
   this.renderer.setClearColor('#000000');
   this.renderer.setSize(window.innerWidth,window.innerHeight);
   this.renderer.setPixelRatio( window.devicePixelRatio );
  
   this.camera = new THREE.OrthographicCamera(window.innerWidth/-2,window.innerWidth/2,window.innerHeight/2,window.innerHeight/-2,0.1,1000);
   this.camera.zoom = 89.5201840247286;
   this.camera.position.set(12.90, 13.94, 13.73);
   this.camera.rotation.set(-0.84196,0.55052,0.53001);
   this.camera.updateProjectionMatrix();
   this.cameraControls = new CameraControls( this.camera, this.renderer.domElement);
   this.cameraControls.dollySpeed = 0.5;
   this.cameraControls.maxZoom = 150;
   this.cameraControls.minZoom = 87;
   const smaa = new SMAAPass(2,2);
   smaa.renderToScreen = true;
   const renderPass = new RenderPass(this.scene,this.camera);
   this.composer = new EffectComposer(this.renderer);
   this.composer.addPass(smaa);
   this.composer.addPass(renderPass);

   this.scene.background = this.props.models.textures.background;
   this.mount.appendChild(this.renderer.domElement);

   const directionalLight = new THREE.DirectionalLight();
   directionalLight.position.set(0,200,0);
   directionalLight.rotation.set(25,0,0)
   const hemisphere = new THREE.HemisphereLight();
   //this.scene.add(this.camera);
   this.scene.add(directionalLight);
   this.scene.add(hemisphere);
   this.start();

   setTimeout(()=>{
    window.addEventListener('click',this.blockClicked);
    window.addEventListener("touchend",this.blockClicked)
    if(!this.props.isPending){
      const world_v2 = new WorldGeneration_V2({
                                            config: this.props.config,
                                            models: this.props.models,
                                            scene: this.scene,
                                            worldSize: 20,
                                            factions: ["Xin-xi","Imperius"],
                                            state: [this.props.changeWorldState]
                                              });
      world_v2.genWorld();
  }
   },3000);

   window.addEventListener('resize',this.handleResize);
  }
  /**
   * @memberof App
   */
  componentWillUnmount(){
    // save the world array to storge
    cancelAnimationFrame(this.frameId);
    this.mount.removeChild(this.renderer.domElement);
  }
  /**
   * starts the requestAnimation
   *
   * @memberof App
   */
  start = () => {
    if(!this.frameId){
      this.frameId = requestAnimationFrame(this.animate)
    }
  } 
  /**
   * Handles the animations of THREE and the world
   *
   * @memberof App
   */
  animate = () =>{
    const delta = this.clock.getDelta();
    this.cameraControls.update( delta );
    //this.renderer.render(this.scene,this.camera);
    this.composer.render(delta)
    this.frameId = window.requestAnimationFrame(this.animate);
  }
  /**
   * handler for the Window resize event
   *
   * @memberof App
   */
  handleResize = (e) => {
    this.renderer.setSize(window.innerWidth,window.innerHeight);
    this.renderer.setPixelRatio( window.devicePixelRatio );
   // this.composer.setSize(window.innerWidth,window.innerHeight);
    //this.composer.setPixelRatio( window.devicePixelRatio );
    this.camera.aspect = window.innerWidth/window.innerHeight;
    this.camera.updateProjectionMatrix();
  }
  handleSlectedState(bool){
    this.setState({isBlockSlected: bool})
  }
  /**
   * Used to slect block with in the World
   *
   * @memberof App
   */
  blockClicked = ev =>{
    // this func should be reworked
    if(!this.props.hovering){

      const mouse2D = new THREE.Vector2((ev.clientX/window.innerWidth)*2-1,-(ev.clientY/window.innerHeight)*2+1)
      this.raycaster.setFromCamera(mouse2D, this.camera);
      const intersects = this.raycaster.intersectObjects(this.scene.children);
      const userSlector = this.scene.children.find(item=>{
        return item.userData.user === this.props.playerData[this.props.currentPlayer].faction
      });

      if(this.props.hasSlectedBlock){
        if(intersects.length > 0){
          if( intersects[0].object.userData.type === "Slector" ){
            this.props.hasSlected(false); 
            userSlector.position.set(0,-1,0);
            this.setState({ slectedData: initalSlectedState})
            this.handleSlectedState(false);
          }else{
            this.props.hasSlected(true)
          }
        }
      }
       
            if(intersects.length > 0){
              const data = intersects[0].object;
              userSlector.position.set(data.position.x,data.position.y,data.position.z);
              if(data.userData.attached){
                this.setState({ slectedData: data.userData});
                this.handleSlectedState(true);
                this.props.hasSlected(true);
                
              }
            }else{
              userSlector.position.set(0,-1,0);
              this.setState({ slectedData: initalSlectedState})
              this.handleSlectedState(false);
              this.props.hasSlected(false);
            }
  
  
    }   
  }
  /**
   * React render
   */
  render(){ 
    let {score,stars,turn,placement,image,state} = this.props.playerData[this.props.currentPlayer];
    
    return (
       <div id="three-scene" ref={mount => {this.mount = mount}}>
         <GameInfo score={score} stars={stars} turn={turn}/> 
         {this.state.isBlockSlected ? <BlockUI update={(bool)=>{this.handleSlectedState(bool)}} name={this.state.slectedData.type} blockData={this.state.slectedData} isHover={this.props.isHovering}/> : <GameNav placement={placement} image={image} state={state} isHover={this.props.isHovering}/>}
       </div>
    );
    }

}
export default connect(mapStateToProps, mapDispatchToProps)(App);



