import { combineReducers } from 'redux';
import * as constants from './constants';

const intialLoadingState = {isPending: false,error: "",models: {},config: [], progress: {item: 0 , of: 0}};
const worldLoadingintial = { isLoading: false, error: ""};
const intialGameState = {location: "MAINMENU"};
const intialHoverOverUI = {
    hovering: false,
    hasSlected: false
};
const intialStateGame = {
    currentPlayer: 0,
    playersData: [{faction: "Imperius", score:0 ,stars:{get: 0, have:0} ,turn:0, playerName: "", placement:"1st", image:"ImperiusHead", state:"next"}]
};
export const gameStateManager = (state=intialStateGame,action={})=>{
    switch (action.type) {
        case constants.GAME_STATE:
            return Object.assign({},state,{currentPlayer: action.payload.currentPlayer, playersData: action.payload.players})
        default:
            return state;
    }
}

export const isHovering = (state=intialHoverOverUI,action={})=>{
    switch (action.type) {
        case constants.HOVER_OVER_UI:
         return Object.assign({},state,{hovering: action.payload});
        case constants.HAS_SLECTED_BLOCK:
            return Object.assign({},state,{hasSlected: action.payload})
        default:
            return state;
    }
}
export const gameState = (state=intialGameState,action={})=>{
    switch (action.type) {
        case constants.GAME_STATE_GAME:
            return Object.assign({},state,{location: "GAME"});
        case constants.GAME_STATE_LOADER:
            return Object.assign({},state,{location: "LOADER"});
        case constants.GAME_STATE_MAINMENU:
            return Object.assign({},state,{location: "MAINMENU"});
        case constants.GAME_STATE_ABOUT:
            return Object.assign({},state,{location: "ABOUT"});
        default:
            return state;
    }
}
export const worldLoading = (state=worldLoadingintial, action={})=>{
    switch (action.type) {
        case constants.WORLD_LOADING_ERROR:
            return Object.assign({},state,{error: action.payload, isLoading: false});
            case constants.WORLD_LOADING_PENDING:
                return Object.assign({},state,{isLoading: action.payload});
            case constants.WORLD_LOADING_FINISH:
                return Object.assign({},state,{isLoading:action.payload})
        default:
            return state;
    }
}
export const gameLoading = (state=intialLoadingState, action={})=>{
    switch (action.type) {
        case constants.GAME_LOADING_ERROR:
            return Object.assign({},state,{error: action.payload});
        case constants.GAME_LOADING_PENDING:
            return Object.assign({},state,{isPending: true});
        case constants.GAME_LOADING_FINISH:
            return Object.assign({},state,{isPending: false, models: action.payload.models, config: action.payload.config})
        case constants.GAME_LOADING_PROGRESS: 
            return Object.assign({},state,{isPending: true, progress: { item: action.payload.item, of: action.payload.of}});
        default:
            return state;
    }
}
export const rootReducer = combineReducers({gameLoading, worldLoading,gameState,isHovering,gameStateManager});
