import { Mesh } from 'three';
/**
 *  Class For Creating blocks for the world map
 *
 * @param models - the loaded OBJ and MTL object
 */
export default class CREATE {
    constructor(models) {
        /**
         *  This Method return a Cloud block;
         *
         * @param pos - {x, y ,z}
         *
         * @returns {mesh} a THREEJS Mesh Object
         */
        this.Cloud = (pos) => {
            const { cloud } = this.models;
            let mesh = new Mesh(cloud.geometry, cloud.material);
            mesh.name = "cloud";
            mesh.userData = {
                type: "Cloud",
                faction: 'none'
            };
            mesh.position.set(pos.x, pos.y, pos.z);
            return mesh;
        };
        /**
         *  This Method return a Water block;
         *
         * @param {number} pos - {x, y ,z}
         * @param {string} faction - The faction to witch the block belongs with
         * @param {number} variation - The variation of the block
         * @param {number} rotation - rotation of the block
         * @returns {Mesh} a THREEJS Mesh Object
         */
        this.Water = (pos, faction = null, variation = 0, rotation = 0) => {
            const { water } = this.models;
            let mesh = new Mesh(water.geometry[variation], water.material[variation]);
            mesh.name = "water_"+variation;
            mesh.userData = {
                type: "Water",
                faction: faction,
                attached: [],
                canBuild: [],
                variation: variation
            };
            mesh.position.set(pos.x, pos.y, pos.z);
            mesh.rotateY( Math.PI * rotation);
            return mesh;
        };
        /**
         *  This Method return a Field block;
         *
         * @param pos - {x, y ,z}
         * @param {string} faction - The faction to witch the block belongs with
         * @param {number} variation - The variation of the block
         * @returns {Mesh} a THREEJS Mesh Object
         */
        this.Field = (pos, faction = null, variation = 0) => {
            const { field } = this.models;
            let mesh = new Mesh(field.geometry, field.material);
            mesh.name = "base_field";
            mesh.userData = {
                type: "Field",
                faction: faction,
                attached: [],
                canBuild: [],
                variation: variation
            };
            mesh.position.set(pos.x, pos.y, pos.z);
            return mesh;
        };
        /**
         *  This Method return a Field block;
         *
         * @param pos - {x, y ,z}
         * @param {string} faction - The faction to witch the block belongs with
         * @param {number} variation - The variation of the block
         * @returns {Mesh} a THREEJS Mesh Object
         */
        this.Tree = (pos, faction = null, variation = 0) => {
            const { tree } = this.models;
            let mesh = new Mesh(tree.geometry[variation], tree.material[variation]);
            mesh.name = "tree_"+variation;
            mesh.userData = {
                type: "Tree",
                faction: faction,
                attached: [],
                canBuild: [],
                variation: variation
            };
            mesh.position.set(pos.x, pos.y, pos.z);
            return mesh;
        };
        
        this.Wall = (pos, faction = null, variation = 0,rotation = 0) => {
            const { wall } = this.models;
            let mesh = new Mesh(wall.geometry[variation], wall.material[variation]);
            mesh.name = "wall_"+variation;
            mesh.userData = {
                type: "Wall",
                faction: faction,
                attached: [],
                canBuild: [],
                variation: variation
            };
            mesh.position.set(pos.x, pos.y, pos.z);
            mesh.rotateY(Math.PI * rotation);
            return mesh;
        };
        /**
         *  This Method return a Ocean block;
         *
         * @param pos - {x, y ,z}
         * @param {string} faction - The faction to witch the block belongs with
         * @param {number} variation - The variation of the block
         * @param {number} rotation - The rotation of the block
         * @returns {Mesh} a THREEJS Mesh Object
         */
        this.Ocean = (pos, faction = null, variation = 0, rotation = 0) => {
            const { ocean } = this.models;
            let mesh = new Mesh(ocean.geometry[variation], ocean.material[variation]);
            mesh.name = "ocean_"+variation;
            mesh.userData = {
                type: "Ocean",
                faction: faction,
                attached: [],
                canBuild: [],
                variation: variation
            };
            mesh.position.set(pos.x, pos.y, pos.z);
            mesh.rotateY(Math.PI * rotation);
            return mesh;
        };
        /**
         *  create a Fruit block mesh
         *
         * @param {number} pos - {x, y ,z}
         * @param {string} faction - The faction to witch the block belongs with
         * @param {number} variation - The variation of the block
         * @returns {Mesh} a THREEJS Mesh Object
         */
        this.Fruit = (pos, faction = null, variation = 0) => {
            const { fruit } = this.models;
            let mesh = new Mesh(fruit.geometry[variation], fruit.material[variation]);
            mesh.name = "fruit_"+variation;
            mesh.userData = {
                type: "Fruit",
                faction: faction,
                variation: variation
            };
            mesh.position.set(pos.x, pos.y, pos.z);
            return mesh;
        };
        /**
         *  This Method return a Slector
         *
         * @param {string} user - The faction to who the Slector belongs
         * @param {number} y - The y axis
         * @param {number} variation - The variation of the block
         * @returns {Mesh} a THREEJS Mesh Object
         */
        this.Slector = (user, y, variation = 0) => {
            const { slector } = this.models;
            let mesh = new Mesh(slector.geometry, slector.material[variation]);
            mesh.name = "slector_"+user;
            mesh.userData = {
                type: "Slector",
                user: user,
                variation: variation
            };
            mesh.position.set(0, y, 0);
            return mesh;
        };
        this.models = models;
    }
}
