import React, {Component} from 'react';

export default class ErrorBoundry extends Component{
constructor(props){
 super(props);
 this.state = {
     error: "",
     hasError: false
 }
}
componentDidCatch(e){
 this.setState({ error: e, hasError: true});
}
render(){ 
    return this.state.hasError ? <div>Error</div> : this.props.children
       }

}